.wrapper {
  /* background-color: red; */
  padding: 5% 8%;
  overflow: hidden;
}
.form {
  display: flex;
  flex-direction: column;
  padding: 2% 4%;
  background-color: royalblue;
  border-radius: 6px;
  box-shadow: 0 0 2px 2px white, 0 0 2px 4px royalblue,
    0 0 3px 5px black;
}
.form button {
  padding: 2% 4%;
  margin: 5% 0 0 0;
  font-size: 8vh;
  font-weight: 700;
  border: 4px solid transparent;
  border-radius: 8px;
  color: white;
  background-color: black;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
}
.form button:active {
  border: 4px inset gray;
  color: gray;
}
.form button.rst {
  padding: 0;
  margin: 3% 0;
  height: 5vh;
  font-size: 4vh;
  background-color: crimson;
}
.form input {
  height: 15vh;
  font-size: 12vh;
  padding: 10px;
  border-radius: 8px;
}
.form label {
  font-size: 3vh;
  font-weight: 700;
  padding: 3% 0;
  color: white;
  text-shadow: 0 0 5px black;
}
.outTxt {
  margin: 6% 0;
  padding: 2% 3%;
  border-radius: 6px;
  border-radius: 6px;
  display: none;
}
.green {
  background-color: springgreen;
  box-shadow: 0 0 2px 2px white, 0 0 2px 4px springgreen,
    0 0 10px 0 #111;
}
.red {
  background-color: crimson;
  box-shadow: 0 0 2px 2px white, 0 0 2px 4px crimson, 0 0 10px 0 #111;
}
.outTxt h1 {
  text-transform: uppercase;
  text-align: center;
  font-size: 6vh;
}
.active {
  display: block;
}
@media screen {
  .form button:hover {
    box-shadow: 0 0 20px lightskyblue, 0 0 10px royalblue;
    text-shadow: 0 0 20px lightskyblue, 0 0 10px royalblue;
  }
}
@media screen and (min-width: 1024px) {
  .wrapper {
    padding: 2%;
    max-width: 640px;
    margin: 0 auto;
  }
}
